@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@300;400;700;900&display=swap);
.ae-app{display:grid;grid-gap:2em 0;gap:2em 0;grid-template-columns:1fr 24fr 1fr;grid-template-rows:auto auto 1fr auto;grid-template-areas:"picker picker picker" "lside body rside" "lside debugger rside" "footer footer footer";justify-items:center;margin:0 auto;max-width:min(99%,60em)}.ae-picker{grid-area:picker}.ae-runner{grid-area:body}.ae-blurb{grid-area:footer}.ae-debugger{grid-area:debugger;text-align:left;align-self:stretch;justify-self:stretch;white-space:pre;overflow:auto;max-height:50vh}
.ae-blurb{align-items:center;display:grid;grid-auto-flow:column;grid-template-columns:repeat(3, 10em);justify-items:center}.ae-blurb__box{text-align:center}
.ae-picker{font-size:1.5em}.ae-picker__list{display:flex;flex-flow:row nowrap;justify-content:center;grid-gap:.5em;gap:.5em;margin:.5em;padding:0}.ae-picker__list a{text-decoration:none}.ae-picker__listitem{display:grid;list-style:none;padding:.25em .5em;border:.1em solid #c08aff;border-radius:.25em}.ae-picker__listitem--is-active{background-color:rgba(192, 138, 255, 0.2);color:#fff}
.ae-runner{display:grid;grid-gap:2em;gap:2em;grid-template-columns:1fr 1fr}.ae-runner__desc{grid-column:span 2}.ae-runner__comment{grid-column:span 2}.ae-runner__box{display:grid;grid-auto-flow:column;align-items:center;grid-template-columns:auto 1fr;grid-gap:1em;gap:1em;padding:0 0 1em}.ae-runner__box--is-success{color:#3cc962}.ae-runner__box--is-fail{color:#e66064}.ae-runner__testid{font-size:0.75rem;line-height:.9em;background-color:#5b476b;border-radius:.2em;color:#fff;padding:.5em 1em}.ae-runner__result{font-size:1rem}.ae-runner__result--is-solution{font-size:1.5rem;font-weight:700}
html{background-color:#16141a;color:#dbcedf;display:grid;height:100vh}body{margin:0;font-family:Source Sans Pro;display:grid;line-height:1.33333em;padding:2em}#root{display:grid}h1,h2,h3,h4,h5,h6,h7{margin:0 0 .5em}p{font-size:1rem;margin:0 0 1rem}a{color:#c08aff}a:visited{color:#c08aff}ul{padding:0 0 0 18px;margin:0}
